.el-checkbox+.el-checkbox,
.el-radio+.el-radio {
  margin-left: 10px;
}

.el-checkbox__label {
  padding-left: 2px;
}

.el-radio__label {
  padding-left: 0px;
}

.image_list {
  width: 148px;
  position: relative;
}

.image_list .image_item {
  width: 148px;
  height: 148px;
}

.image_list .uploadimage__item-actions {
  position: absolute;
  width: 100%;
  height: 148px;
  line-height: 148px;
  left: 0;
  top: 0;
  cursor: default;
  text-align: center;
  color: #fff;
  opacity: 0;
  font-size: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
  z-index: 100;
}

.image_list .uploadimage__item-actions:hover {
  opacity: 1;
}

.image_list .uploadimage__item-actions span {
  color: #fff;
  cursor: pointer;
}

.image_list .uploadimage__item-actions i {
  color: #fff;
  font-size: 20px;
}

.image_list .uploadimage__item-actions .uploadimage__item-delete {
  position: static;
  font-size: inherit;
  color: inherit;
}

.el-message {
  top: 110px;
}
